.wrapper {
    margin-top: 20px;
}

/* Authentication */
.auth {
    margin-top: 50px;
    text-align: center;
}
  
.logo {
    text-align: center;
    font-weight: 100;
    font-size: 80px;
    -webkit-user-select: none;
    /* Chrome all / Safari all */
    -moz-user-select: none;
    /* Firefox all */
    -ms-user-select: none;
    /* IE 10+ */
    user-select: none;
    /* Likely future */
}

a.logo {
    color: #5B5B5B;
}

a {
    cursor: pointer;
}

.auth .card {
    width: 400px;
    margin: 0 auto;
}

/* WRITE */

.write .materialize-textarea {
    padding: 0px;
    padding-bottom: 36px;
    margin-bottom: 0px;
    font-size: 18px;
}

.write .card-content {
    padding-bottom: 10px;
}

.write .card-action {
    text-align: right;
}

/* Memo */

.memo .info {
    font-size: 18px;
    padding: 20px 20px 0px 20px;
    color: #90A4AE;
  }
  
  .memo .info .username {
    color: #263238;
    font-weight: bold;
    cursor: pointer;
  }
  
  .memo .card-content {
    word-wrap: break-word;
  }
  
  .memo .option-button {
      position: absolute;
      right: 20px;
      top: 20px;
  }
  
  .icon-button {
    color: #9e9e9e;
    cursor: pointer;
  }
  
  .icon-button:hover {
    color: #C5C5C5;
  }
  
  .icon-button:hover {
    color: #C5C5C5;
  }
  
  .icon-button:active {
    color: #ff9800;
  }
  
  .memo .card-content {
    font-size: 18px;
  }
  
  .memo .footer {
    border-top: 1px solid #ECECEC;
    height: 45px;
  }
  
  .star {
    position: relative;
    left: 15px;
    top: 11px;
  }
  
  .star-count {
    position: relative;
    left: 20px;
    top: 4px;
    font-size: 13px;
    font-weight: bold;
    color: #777;
  }

.empty-page {
    font-size: 30px;
    text-align: center;
    color: #4D4D4D;
}

.wall-info {
    font-size: 30px;
    text-align: center;
}

/* SEARCH */
.search-screen {
    position: fixed;
    overflow-y: none;
    left: 0px;
    top: 0px;
    height:100%;
    width:100%;
    background-color: rgba(0, 0, 0, 0.70);
    z-index: 99;
}

.search-screen input {
    text-align: center;
    font-size: 50px;
    line-height: 80px;
    margin-top: 10vw;
    height: 80px;
    font-weight: 200;
}

.search-screen .btn {
    margin-top: 14px;
    margin-right: 20px;
}

ul.search-results {
    text-align: center;
    font-size: 30px;
    margin-top: 0px;
}

.search-results a {
    padding: 10px;
    display: block;
    color: white;
}

.search-results a + a {
    border-top: 1px solid #5F5F5F;
}

.search-results a:hover {
    background-color: rgba(255, 255, 255, 0.10);
}

@media screen and (max-width: 480px) {
    .auth .card {
        width: 100%;
    }

    .logo {
        font-size: 60px;
    }
}



.auth .header {
    font-size: 18px;
}

.auth .row {
    margin-bottom: 0px;
}

.auth .username {
margin-top: 0px;
}

.auth .btn {
    width: 90%;
}

.auth .footer {
    border-top: 1px solid #E9E9E9;
    padding-bottom: 21px;
}

/* Header */
nav,
nav ul.line li,
nav ul.line li a {
    height: 4rem;
    line-height: 4rem;
}

/* 웹 표준 */

/* 텍스트를 숨깁니다. */
.t-h {  
    position: absolute;
    left: -10000px;
    top:auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
}